  const removeBoldTags = (text) => {
    // 블로그 호출할 때, <b>, &quot;, apos;, &lt; &amp;, &gt; 태그 제거
    const boldTagRegex = /<\/?b>/gi;
    let cleanedText = text?.replace(boldTagRegex, "");

    cleanedText = cleanedText?.replace(/<br\s*\/?>/gi, ""); // <br> 및 <br /> 태그 제거 추가
    cleanedText = cleanedText?.replace(/&quot;/g, "");
    cleanedText = cleanedText?.replace(/&apos;/g, "");
    cleanedText = cleanedText?.replace(/&lt;/g, "");
    cleanedText = cleanedText?.replace(/&amp;/g, "");
    cleanedText = cleanedText?.replace(/&gt;/g, "");

    return cleanedText;
  };
